* {
  box-sizing: border-box;
}

.chat {
  max-width: 400px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0px 0px 5px #ccc;
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 999;
}
.chat__messages {
  overflow-y: auto;
}
.chat__input {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 15px;
}
.chat__input-field {
  flex-grow: 1;
  padding: 10px;
  border: none;
  border-radius: 5px;
}
.chat__send-button {
  padding: 10px 20px;
  background-color: #007BFF;
  border: none;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
}
.chat__send-button:active {
  background-color: #0056b3;
}
.chat__canvas {
  width: 200px;
  height: 100px;
  align-self: center;
  justify-self: end;
}
.chat__canvas_hidden {
  display: none;
}

.message {
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
}

.user {
  background-color: #e1f5fe;
}

.bot {
  background-color: #c8e6c9;
}